.button {
  display: inline-block;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  overflow: visible;
  font-family: inherit;
  color: #333;
  background: #fff none;
  margin: 0 10px;
}

.button:focus,
.button:hover {
  background-color: #e6e6e6;
  border-color: #adadad;
  text-decoration: none;
}
